import './Main.css';

import Header from '../component/Header';

import { Link } from "react-router-dom";

const Main = () => {
    return (
        <div className='main_div'>
            <Header />
            <div className='screen_main'>
                <Link to='/prac'>
                    <div className='button_main' id='topbutton'>
                        <div className='wrap_main'>
                            <img alt="" src="/img/doki_prac.png" className='contentimg' />
                            <p className='text_main'>  </p>
                        </div>
                    </div>
                </Link>
                <Link to='/chal'>
                    <div className='button_main'>
                        <div className='wrap_main'>
                            <img alt="" src="/img/doki_chal.png" className='contentimg' />
                            <p className='text_main'>  </p>
                        </div>
                    </div>
                </Link>
            </div>

        </div>
    );
};
export default Main;



