import Header from '../../component/Header';
import './CC1.css';

import { useRef, useState, useEffect } from 'react';
import { Link } from "react-router-dom";

const CC1 = () => {

    let [cate, setCate] = useState('cate1');
    const [buy, setBuy] = useState([]);
    const buyid = useRef(0);
    let [totalprice, setTotalPrice] = useState(0);
    const [viewmodal, setViewModal] = useState(false);
    let [questmenu, setQuestMenu] = useState('');
    let [questprice, setQuestPrice] = useState(0);
    let [queststate, setQuestState] = useState(true);
    let [questmodal, setQuestModal] = useState(true);

    let [rannum, setRanNum] = useState(0);
    useEffect(() => {
        setRanNum(Math.floor(Math.random() * 24));
    }, []);

    function NewBuy(menuname, price) {
        const newmenu = {
            id: buyid.current,
            menuname,
            price
        }
        buyid.current += 1;
        setTotalPrice(totalprice + price);
        setBuy([...buy, newmenu]);
    }
    function Categori({ categori }) {
        const menulist = [
            { id: 1, cate: 1, menuname: '', price: 4000, imgsrc: '/img/cafe/1americano.png' },
            { id: 2, cate: 1, menuname: '', price: 4500, imgsrc: '/img/cafe/1cafelatte.png' },
            { id: 3, cate: 1, menuname: '', price: 4500, imgsrc: '/img/cafe/1vanilla.png' },
            { id: 4, cate: 1, menuname: '', price: 5500, imgsrc: '/img/cafe/1caramel.png' },
            { id: 5, cate: 1, menuname: '', price: 6000, imgsrc: '/img/cafe/1mocha.png' },
            { id: 6, cate: 1, menuname: '', price: 6000, imgsrc: '/img/cafe/1whitemocha.png' },

            { id: 7, cate: 2, menuname: '', price: 6000, imgsrc: '/img/cafe/2dalgona.png' },
            { id: 8, cate: 2, menuname: '', price: 4500, imgsrc: '/img/cafe/2greentea.png' },
            { id: 9, cate: 2, menuname: '', price: 4500, imgsrc: '/img/cafe/2hongsi.png' },
            { id: 10, cate: 2, menuname: '', price: 4500, imgsrc: '/img/cafe/2mintchoco.png' },
            { id: 11, cate: 2, menuname: '', price: 4500, imgsrc: '/img/cafe/2strawberry.png' },
            { id: 12, cate: 2, menuname: '', price: 4500, imgsrc: '/img/cafe/2sweetpotato.png' },

            { id: 13, cate: 3, menuname: '', price: 4000, imgsrc: '/img/cafe/3chamomile.png' },
            { id: 14, cate: 3, menuname: '', price: 4000, imgsrc: '/img/cafe/3hibiscus.png' },
            { id: 15, cate: 3, menuname: '', price: 4000, imgsrc: '/img/cafe/3peppermint.png' },
            { id: 16, cate: 3, menuname: '', price: 4000, imgsrc: '/img/cafe/3rooibos.png' },
            { id: 17, cate: 3, menuname: '', price: 4000, imgsrc: '/img/cafe/3icelemon.png' },
            { id: 18, cate: 3, menuname: '', price: 4000, imgsrc: '/img/cafe/3icepeach.png' },

            { id: 19, cate: 4, menuname: '', price: 5000, imgsrc: '/img/cafe/4cakecheese.png' },
            { id: 20, cate: 4, menuname: '', price: 5000, imgsrc: '/img/cafe/4cakechoco.png' },
            { id: 21, cate: 4, menuname: '', price: 2800, imgsrc: '/img/cafe/4cookiechoco.png' },
            { id: 22, cate: 4, menuname: '', price: 2800, imgsrc: '/img/cafe/4cookiewhite.png' },
            { id: 23, cate: 4, menuname: '', price: 3200, imgsrc: '/img/cafe/4croffle.png' },
            { id: 24, cate: 4, menuname: '', price: 3200, imgsrc: '/img/cafe/4saltbread.png' },
        ]

        useEffect(() => {
            setQuestMenu(menulist[rannum].menuname);
            setQuestPrice(menulist[rannum].price);
            // eslint-disable-next-line react-hooks/exhaustive-deps
        }, []);

        function ShowMenu({ menu }) {
            return (
                <div className='menu_CC1'
                    onClick={
                        () => {
                            NewBuy(menu.menuname, menu.price)

                            const dummy = totalprice + menu.price
                            if ((menu.menuname !== questmenu) || (dummy !== questprice)) {
                                setQuestState(false)

                            }
                        }
                    }
                >
                    <img alt={menu.menuname} src={menu.imgsrc} className='cafeimg' />
                    <h5 className='menuname_CC1'>{menu.menuname}</h5>
                    <h5 className='menuprice_CC1'>{menu.price}</h5>
                </div>
            )
        }


        return (
            <div className='menuwrap_CC1'>
                {menulist.filter(menu => menu.cate === categori).map((menu) => (
                    <ShowMenu key={menu.id} menu={menu} />
                ))}
            </div>
        )

    }

    return (
        <div>
            <Header />
            <div className='screen_CC1'>
                <nav className='nav_CC1'>
                    <ul>
                        <li id={cate === 'cate1' ? 'select_CC1' : ''}
                            onClick={
                                () => {
                                    setCate('cate1');
                                }
                            }
                        ></li>
                        <li id={cate === 'cate2' ? 'select_CC1' : ''}
                            onClick={
                                () => {
                                    setCate('cate2')
                                }
                            }
                        ></li>
                        <li id={cate === 'cate3' ? 'select_CC1' : ''}
                            onClick={
                                () => {
                                    setCate('cate3')
                                }
                            }
                        >(Tea)</li>
                        <li id={cate === 'cate4' ? 'select_CC1' : ''}
                            onClick={
                                () => {
                                    setCate('cate4')
                                }
                            }
                        ></li>
                    </ul>
                </nav>
                {(cate === 'cate1') ? <Categori categori={1} /> : null}
                {(cate === 'cate2') ? <Categori categori={2} /> : null}
                {(cate === 'cate3') ? <Categori categori={3} /> : null}
                {(cate === 'cate4') ? <Categori categori={4} /> : null}
                <MenulistCC1 />
                {viewmodal ? <PayModalCC1 /> : null}
                {questmodal ? <QusetModalCC /> : null}
                <div className='questbutton'
                    onClick={
                        () => {
                            setQuestModal(true);
                        }
                    }>
                    
                </div>
            </div>
        </div>
    );

    function MenulistCC1() {
        return (
            <div className='menulist_CC1'>
                <div className='listl_CC1'>
                    <AddMenu buy={buy} />
                </div>
                <div className='listr_CC1'>
                    <button className='cancel_CC1'
                        onClick={
                            () => {
                                buyid.current = 0;
                                setBuy([]);
                                setTotalPrice(0);
                                setQuestState(true);
                            }
                        }
                    > </button>
                    <div className='totalpricediv_CC1'>
                        <div className='totalprice_CC1'>
                            {totalprice}
                        </div>
                    </div>
                    <button className='pay_CC1'
                        onClick={
                            () => {
                                if (totalprice !== 0) {
                                    setViewModal(true)
                                }
                                else {
                                    alert(' .')
                                }
                            }
                        }
                    ></button>
                </div>
            </div>
        )
    }

    function AddMenu({ buy }) {
        return (
            <div>
                {buy.map((m) => (
                    <div className='menulistwrap_CC1' key={m.id}>
                        <div className='menulistname_CC1'>{m.menuname}</div>
                        <div className='menulistprice_CC1'>{m.price}</div>
                    </div>
                ))}
            </div>
        )

    }

    function PayModalCC1() {
        return (
            <div className='paymodal_CC1'>
                <div className='modalscreen_CC1'>
                    <div className='modalhead_CC1'>
                          
                    </div>
                    <div className='modalcontent_CC1'>
                        <AddMenu buy={buy} />
                    </div>
                    <div className='modalbutton_CC1'>
                        <div className='return_CC1'
                            onClick={
                                () => {
                                    setViewModal(false);
                                }
                            }>
                            
                        </div>
                        <Link to={queststate ? '/' : ''}>
                            <div className='here_CC1'
                                onClick={
                                    () => {
                                        if (!queststate) {
                                            alert("    .")
                                        }
                                        else{
                                            alert("  .   .")
                                        }
                                    }
                                }>
                                
                            </div>
                        </Link>
                        <Link to={queststate ? '/' : ''}>
                            <div className='togo_CC1'
                                onClick={
                                    () => {
                                        if (!queststate) {
                                            alert("    .")
                                        }
                                        else{
                                            alert("  .   .")
                                        }
                                    }
                                }>
                                
                            </div>
                        </Link>
                    </div>
                </div>
            </div>
        )
    }

    function QusetModalCC() {
        return (
            <div className='questmodal'>
                <div className='questmodalscreen'>
                    <div className='questmodalhead'>
                        
                    </div>
                    <div className='questmodalcontent'>
                        {questmenu} <br />
                         
                    </div>
                    <div className='questmodalbutton'>
                        <div className='returnquest'
                            onClick={
                                () => {
                                    setQuestModal(false);
                                }
                            }>
                            
                        </div>
                    </div>
                </div>
            </div>
        )
    }

};
export default CC1

/*  :  */