import './PT1.css';
import Header from '../../component/Header';

import { useState } from 'react';
import { Link } from "react-router-dom";

import DatePicker from 'react-datepicker';
import "react-datepicker/dist/react-datepicker.module.css";
import { ko } from 'date-fns/esm/locale';

const PT1 = () => {

    let [locatemap, setLocatemap] = useState(false);
    let [step, setStep] = useState('step1');
    let [startP, setStartP] = useState('');
    let [endP, setEndP] = useState('');
    const today = new Date();
    const week = ['', '', '', '', '', '', '', ''];
    let hour = today.getHours().toString();
    if (hour.length < 2){
        hour = "0" + hour
    }
    let min = today.getMinutes().toString();
    if (min.length < 2){
        min = "0" + min
    }
    const formattedDate = `${today.getFullYear()} ${today.getMonth() + 1} ${today.getDate()} (${week[today.getDay()]}) ${hour}:${min}`;
    let [selectedDate, setSelectedDate] = useState(formattedDate);

    return (
        <div>
            <Header />
            <div className='head_PT1'>
                <p id='headtextpt1'> </p>
            </div>

            <div className='screen_PT1'>
                <div className='locate'>
                    <p className='text'></p>
                    <p className= 'locate_sel'
                        id = {step === 'step1' ? 'step_color' : ''}
                        onClick={
                            () => {
                                if (step === 'step1'){
                                    setLocatemap(true);
                                    setStep('step2');
                                }                                
                            }
                        }
                        >
                        {startP}</p>

                </div>

                <div className='locate'>
                    <p className='text'></p>
                    <p className='locate_sel'
                        id = {step === 'step3' ? 'step_color' : ''}
                        onClick={
                        () => {
                            if (step === 'step3'){
                                setLocatemap(true);
                                setStep('step4');
                            }
                        }
                    }>
                        {endP}</p>
                </div>
                <div className='locatelist' id='list_start'>
                    { (step === 'step2') && (locatemap === true) ? <Locatemap /> : null}
                </div>
                <div className='locatelist' id='list_start'>
                    { (step === 'step4') && (locatemap === true) ? <Locatemap /> : null}
                </div>
                <div className='map'>  </div>
                <div className='date'
                    id = {step === 'step5' ? 'step_color' : ''}
                    >
                    <p className='text'></p>
                    <p className = {step === 'step5' ? 'displaynone' : ''}
                    >{selectedDate}</p>
                    <Example />
                </div>
                <div className='info'
                    id = {step === 'step6' ? 'step_color' : ''}
                    onClick={
                        () => {
                            if (step === 'step6'){
                                setLocatemap(true);
                                setStep('step7');
                            }
                        }
                    }
                    >
                    <p className='text'>   </p>
                    <p> 1</p>
                    <div>
                    { (step === 'step7') ? <Numage /> : null}
                    </div>
                </div>
                
                <Link to={step === 'step7' ? '/prac/train2' : ''}>
                <div className='inquiry'>
                    <p className='inquiry2' id = {step === 'step7' ? 'step_color' : ''}> </p>
                </div>
                </Link>

            </div>
        </div>
    );

    function Locatemap() {
        return (
            <div className='locate_map'>
                <div></div>
                <div></div>
                <div id = {step === 'step2' ? 'step_color' : ''}
                    onClick={
                        () => {
                            if (step === 'step2'){
                                setStartP('');
                                setStep('step3');
                            }
                            
                        }
                    }></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div>EXPO</div>
                <div></div>
                <div id = {step === 'step4' ? 'step_color' : ''}
                    onClick={
                        () => {
                            if (step === 'step4'){
                                setEndP('');
                                setStep('step5');
                            }
                        }
                    }></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
            </div>
        )
    }


    function Example() {
        const [startDate, setStartDate] = useState(new Date());
        return (
          <DatePicker
            selected={startDate}
            onChange={
                date => {
                    setStartDate(date);
                    setStep('step6');
                    const selDate = `${date.getFullYear()} ${date.getMonth() + 1} ${date.getDate()} (${week[date.getDay()]}) ${hour}:${min}`;
                    setSelectedDate(selDate);
                }
            }
            locale={ko}
            dateFormat="yyyy M d (eee) HH:mm"
            timeIntervals={15}
            withPortal
            className={step === 'step5' ? 'datapicker' : 'displaynone'}
            minDate={new Date(today).setDate(today.getDate() + 1)}
            maxDate={new Date(today).setDate(today.getDate() + 1)}
          />
        )
    }

    function Numage(){
        return(
            <div className='locate_map'>
                <div>(13 )</div>
                <div><button>-</button>&nbsp;&nbsp; 1 &nbsp;&nbsp;<button>+</button></div>
                <div>( 6~12)</div>
                <div><button>-</button>&nbsp;&nbsp; 0 &nbsp;&nbsp;<button>+</button></div>
                <div>( 6 )</div>
                <div><button>-</button>&nbsp;&nbsp; 0 &nbsp;&nbsp;<button>+</button></div>
                <div>( 65 )</div>
                <div><button>-</button>&nbsp;&nbsp; 0 &nbsp;&nbsp;<button>+</button></div>
                <div> </div>
                <div><button>-</button>&nbsp;&nbsp; 0 &nbsp;&nbsp;<button>+</button></div>
                <div> </div>
                <div><button>-</button>&nbsp;&nbsp; 0 &nbsp;&nbsp;<button>+</button></div>
            </div>
        )

    }

};
export default PT1


