import './Detail.css';
import Header from '../component/Header';

import { Link } from "react-router-dom";

const Prac = () => {
        return (
                <div>
                        <Header />

                        <div className='screen_detail'>
                                <Link to='/prac/train1'>
                                        <div className='button' id='topbutton'>
                                                <div className='wrap'>
                                                        <img alt=" " src="/img/trainimg.png" className='contentimg' />
                                                </div>
                                        </div>
                                </Link>
                                <Link to='/prac/cafe1'>
                                        <div className='button'>
                                                <div className='wrap'>
                                                        <img alt=" " src="/img/cafeimg.png" className='contentimg' />
                                                </div>
                                        </div>
                                </Link>
                        </div>

                </div>
        );
};
export default Prac